
#app {
  font-family: "\5FAE\8F6F\96C5\9ED1", "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}

.cloud-home {
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.cloud-home .layout-header {
    height: 50px;
}
.cloud-home .layout-container {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.cloud-home .layout-container .layout-right {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      overflow-y: auto;
}

.cloud-header {
  width: 100%;
  height: 50px;
  background: #373D41;
}

.cloud-sidebar {
  height: 100%;
}
.cloud-sidebar .mian-bar {
    float: left;
    height: 100%;
    background: #333744;
}
.cloud-sidebar .mian-bar .narrow-wrapper {
      width: 180px;
      height: 30px;
      background: #4a5064;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      cursor: pointer;
}
.cloud-sidebar .mian-bar .narrow-wrapper .narrow-icon {
        font-size: 12px;
        color: #aeafa7;
}
.cloud-sidebar .mian-bar .narrow-wrapper:hover .narrow-icon {
      color: #fff;
}
.cloud-sidebar .mian-bar .unfold-wrapper {
      width: 50px;
      height: 30px;
      background: #4a5064;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      cursor: pointer;
}
.cloud-sidebar .mian-bar .unfold-wrapper .unfold-icon {
        font-size: 20px;
        color: #aeafa7;
}
.cloud-sidebar .mian-bar .unfold-wrapper:hover .unfold-icon {
      color: #fff;
}
.cloud-sidebar .mian-bar .first-menu-wrapper {
      color: #fff;
      font-size: 12px;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-title {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        height: 40px;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        background: #42485b;
        cursor: pointer;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-title img {
          margin-left: 18px;
          margin-right: 8px;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-title:hover {
        background: #00c1de;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-list .submenu {
        color: #aeb9c2;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-list .submenu i {
          display: inline-block;
          width: 16px;
          height: 16px;
          margin: 0 17px;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-list .submenu:hover {
        background: #4a5064;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-list .submenu-active {
        background: #00c1de;
        color: #fff;
}
.cloud-sidebar .mian-bar .first-menu-wrapper .item-menu-wrapper .item-menu-list .submenu-active:hover {
        background: #00c1de;
        color: #fff;
}
.cloud-sidebar .sub-bar {
    float: left;
    width: 180px;
    height: 100%;
    -webkit-transition: width 0.2s;
    transition: width 0.2s;
    background: #eaedf1;
    position: relative;
}
.cloud-sidebar .sub-bar .sub-fold-wrapper {
      width: 20px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background: #f7f7f7;
      cursor: pointer;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 2;
      margin: auto 0;
}
.cloud-sidebar .sub-bar .sub-fold-wrapper .sub-fold-icon {
        font-size: 16px;
        color: #546478;
}
.cloud-sidebar .sub-bar .sub-unfold-wrapper {
      width: 20px;
      height: 50px;
      line-height: 50px;
      text-align: center;
      background: #d9dee4;
      cursor: pointer;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      z-index: 2;
      margin: auto 0;
}
.cloud-sidebar .sub-bar .sub-unfold-wrapper .sub-unfold-icon {
        font-size: 16px;
        color: #546478;
}
.cloud-sidebar .sub-bar .sub-header-title {
      height: 70px;
      line-height: 70px;
      background: #d9dee4;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      font-weight: 600;
      font-size: 12px;
      text-indent: 20px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
}
.cloud-sidebar .sub-bar .sub-menu-list {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      padding-top: 70px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      overflow-y: auto;
}
.cloud-sidebar .sub-bar .sub-menu-list .sub-menu-item {
        display: block;
        height: 40px;
        line-height: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-indent: 20px;
        color: #333;
        font-size: 12px;
}
.cloud-sidebar .sub-bar .sub-menu-list .sub-menu-item:hover {
        background: #f4f6f8;
}
.cloud-sidebar .sub-bar .sub-menu-list .sub-menu-item-active {
        background: #fff;
}
.cloud-sidebar .sub-bar .sub-menu-list .sub-menu-item-active:hover {
        background: #fff;
}

.cloud-container{
    width: 100%;
    height: 200%;
}


































































































body,dl,dd,ul,ol,h1,h2,h3,h4,h5,h6,pre,form,input,textarea,p,hr,thead,tbody,tfoot,th,td{margin:0;padding:0;}
ul,ol{list-style:none;}
a{text-decoration:none;}
html{-ms-text-size-adjust:none;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none;}
body{line-height:1.5; font-size:14px;}
body,button,input,select,textarea{font-family:'helvetica neue',tahoma,'hiragino sans gb',stheiti,'wenquanyi micro hei',\5FAE\8F6F\96C5\9ED1,\5B8B\4F53,sans-serif;}
b,strong{font-weight:bold;}
i,em{font-style:normal;}
table{border-collapse:collapse;border-spacing:0;}
